/****************************************************************
 *
 *  Tags layout configuration
 *
****************************************************************/
.tag-container {
    margin-block: 64px;
    .tag {
        padding: 16px;
        border: 2px solid black;
        border-radius: 16px;
        text-decoration: none;
        background: white;
        color: black;
        margin-left: 8px;
    
        .counter {
            margin-inline-start: 32px;
        }
    }
    
    .tag:hover {
        background: black;
        color: white;
    }
}