/****************************************************************
 *
 *  Post layout configuration
 *
****************************************************************/
.post {
    padding: 16px;
    width: 100%;

    .post-title {
        margin-left: 16px;
        font-size: 52px;
    }

    .post-meta {
        margin-top: 16px;
        display: flex;
        
        .post-posted-at {   padding: 16px;     }
        
        .post-tags {
            flex: 1;
            display: flex;
            justify-content: right;
            
            .tag {
                padding: 16px;
                border: 2px solid black;
                border-radius: 16px;
                text-decoration: none;
                background: white;
                color: black;
                margin-left: 8px;
            }

            .tag:hover {
                background: black;
                color: white;
            }
        }
    }

    .post-content {
        margin-left: 32px;

        *:not(:last-child) {
            margin-bottom: 16px;
        }

        h1, h2, h3, h4, h5, h6 {
            margin-top: 48px;
        }

        blockquote {
            margin-block: 48px;
            padding-block: 16px;
            padding-left: 48px;
            border-left: 8px solid black;

            p {
                margin: 0;
            }
        }

        img {
            display: block;
            margin-inline: auto;
            width: 50%;
        }

        .rouge-gutter {
            display: block;
            margin-right: 24px;
            margin-bottom: 0;
        }
    }
}