/****************************************************************
 *
 *  Home layout configuration
 *
****************************************************************/
$post-title-size: 32px;
.posts-container {
    padding: 16px;
    width: 100%;

    .post-compact {
        padding-top: 32px;
        padding-bottom: 32px;

        .post-title {
            a {
                font-size: $post-title-size;
                width: 100%;
                display: inline-block;
                text-decoration: none;
                width: 100%;
                padding: 16px;
                background: white;
                color: black;
            }

            a:hover {
                background: black;
                color: white;
            }
        };

        .post-meta {
            margin-top: 16px;
            display: flex;
            
            .post-posted-at {   padding: 16px;     }
            
            .post-tags {
                flex: 1;
                display: flex;
                justify-content: right;
                
                .tag {
                    padding: 16px;
                    border: 2px solid black;
                    border-radius: 16px;
                    text-decoration: none;
                    background: white;
                    color: black;
                    margin-left: 8px;
                }

                .tag:hover {
                    background: black;
                    color: white;
                }
            }

        }

        border-bottom: 2px solid black;
    }
}