/****************************************************************
 *
 *  Page layout configuration
 *
****************************************************************/

///
/// Variables
/// 
$base-font-size: 24px;

* :not(li, ul) {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Quicksand';
}

html, body {    height: 100%;   }

.main-wrapper {
    font-size: $base-font-size;
    display: flex;
    height: 100%;

    .sidebar {
        width: 15%;
        margin-right: 8px;
        display: flex;
        flex-direction: column;

        ul {
            width: 100%;
            margin: 0;
            padding: 0;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        li {
            width: 100%;
            list-style: none;
            
            a {
                display: inline-block;
                text-decoration: none;
                width: 100%;
                text-align: center;
                padding-top: 16px;
                padding-bottom: 16px;
                background: white;
                color: black;
            }

            a:hover {
                background: black;
                color: white;
            }
        }
    }

    .main {
        flex: 1;
        width: 100%;
        display: flex;
        flex-direction: column;

        .main-title {
            padding: 16px;
            color: white;
            background: black;
            font-weight: bolder;
        }

        .content {
            display: flex;
            flex: 1;
        }

        .footer {
            text-align: center;
        }
    }
}

a {
    display: inline-block;
    text-decoration: underline;
    text-decoration-style: dotted;
    color: black;
    background: white;
    padding: 8px;
}

a:hover {
    color: white;
    background: black;
}

p {
    margin-top: 16px;
}

.pager {
    display: flex;
    justify-content: center;
    li {
        list-style: none;
    }
}